$color_1: #111111;
$color_2: #ffffff;
$color_3: #ff0157;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

// body {
//   min-height: 1000px;
// }

p {
  font-weight: 300;
  color: $color_1;
}

// General
section {
  padding: 100px;
}
.row {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  .col50 {
    position: relative;
    width: 48%;
    .imgBx {
      width: 100%;
      min-height: 300px;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
.title {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.titleText {
  color: $color_1;
  font-size: 2em;
  font-weight: 300;
  span {
    display: inline-block;
  }
  span::first-letter {
    font-size: 1.5em;
    font-weight: 700;
    color: $color_3;
  }
}

.btn {
  display: inline-block;
  font-size: 1em;
  color: $color_2;
  background: $color_3;
  padding: 10px 30px;
  margin-top: 20px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.5s;
  &:hover {
    letter-spacing: 6px;
  }
}

// Header
header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  right: 0;
  padding: 40px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s;
  .logo {
    color: $color_2;
    font-weight: 700;
    font-size: 2em;
    text-decoration: none;
    span {
      color: $color_3;
    }
  }
}
header.sticky {
  background: $color_2;
  padding: 10px 100px;
  box-shadow: 0 5px 20px rgba($color_1, 0.1);
  .logo {
    color: $color_1;
  }
  .navbar li a {
    color: $color_1;
  }
}

// Navbar
header .navbar {
  position: relative;
  display: flex;
  li {
    list-style: none;
    margin-left: 30px;
    a {
      text-decoration: none;
      color: $color_2;
      font-weight: 300;
      &:hover {
        color: $color_3;
      }
    }
  }
}

// Menu Togle
.menuTogle {
  display: none;
}

// Banner
.banner {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(../images/bg.jpg) no-repeat center center;
  box-sizing: cover;
  .content {
    max-width: 900px;
    text-align: center;
    h2 {
      font-size: 5em;
      color: $color_2;
    }
    p {
      font-size: 1em;
      color: $color_2;
    }
  }
}

// Menu
.menu {
  .content {
    
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    margin-top: 40px;
    .box {
      width: 340px;
      margin: 20px;
      border: 15px solid $color_2;
      box-shadow: 0 5px 35px rgba($color_1, 0.1);
      .imgBx {
        width: 100%;
        height: 300px;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .text {
        // text-align: center;
        padding: 15px 0 5px;
        h3 {
          color: $color_1;
          font-weight: 400;
        }
      }
    }
  }
}
